////
/// Comment component.
///
/// This is the component for handling individual comments in a comments list.
///
/// @group  Components
/// @author Steffen Bang Nielsen <sbn@retrofitter.dk>
/// @link   https://retrofitter.dk
////

.comment {

	// Type of comment.
	&--type-comment {}

	&--type-ping {}

	// Comment status.
	&--status-unapproved {}

	// Full comment text/content.
	&__content {
		a {
			word-wrap: break-word;
		}
	}

	// Comment under moderation message.
	&__moderation {}

	// Metadata wrapper.
	&__meta {}

	// Individual metadata items.
	&__avatar {}

	&__author {}

	&__permalink {}

	&__date {}

	&__time {}

	&__edit {}

	&__reply {}
}

.bypostauthor {
	display: block;
}
